import { Component, AfterViewChecked, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewChecked{

  isBackground = false;
  isBackgroundChangeInit = false;

  ngOnInit() {
    if(!this.isBackgroundChangeInit){
    //  this.initBackgroundChange();
    }
  }

  ngAfterViewChecked() {
 
  }

  initBackgroundChange() {
    this.isBackgroundChangeInit = true;

    const body = document.getElementsByTagName("body")[0];

    setInterval(()=>{
         body.style.backgroundImage = this.isBackground ? "url('background2.gif')" : "url('background.gif')";
         this.isBackground = !this.isBackground;
       }, 4000);
  }
}
